
import $ from "jquery";

window.addEventListener('DOMContentLoaded', function() {




let wrapHamb = document.querySelector('.wrap_hamb'),
    prevScrollpos = window.pageYOffset;

// when scrolling down, hide the hamburger menu

          window.onscroll = function() {
            let currentScrollPos = window.pageYOffset;
              if (prevScrollpos > currentScrollPos) {
                   wrapHamb.style.display = 'block';

     document.getElementById("navbar").style.opacity = "1";
     document.getElementById("navbar").style.top = "0";
     document.getElementById("wrap_hamb").style.opacity = "1";
     document.getElementById("wrap_hamb").style.marginRight = "0";    

  } else {
     
     document.getElementById("navbar").style.opacity = "0";
     document.getElementById("navbar").style.top = "-80px";
     document.getElementById("wrap_hamb").style.opacity = "0";
     document.getElementById("wrap_hamb").style.marginRight = "-120px";
    
  }
  prevScrollpos = currentScrollPos;
} 



  var is_chrome = navigator.userAgent.toLowerCase().indexOf('chrome') > -1;

  if(is_chrome && window.innerWidth < 575) {
    return false;
    } else {
      $(function() {
  // fade in the header content on scroll down
                
                let documentEl = $(document),
                    fadeElem = $('.fade-scroll');
                
                
                documentEl.on('scroll', function() {
                    let currScrollPos = documentEl.scrollTop();
                  
                    
                    fadeElem.each(function() {
                        var $this = $(this),
                            elemOffsetTop = $this.offset().top;
                        if (currScrollPos > elemOffsetTop) $this.css('opacity', 1 - (currScrollPos-elemOffsetTop)/1500);
                    }); 
                });
                
            });

        }

})

