import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Pslider from './fSlider';
import Images from './images';
import * as serviceWorker from './serviceWorker';
import { Route, BrowserRouter as Router } from 'react-router-dom'
import BrowserRouter from 'react-router-dom/BrowserRouter'


const routing = (
  <Router>
    <div>
      <Route exact path={process.env.PUBLIC_URL + '/'} component={Pslider}/>
      <Route exact path={process.env.PUBLIC_URL + '/images'} component={Images}/>
    </div>
  </Router>
)

ReactDOM.render(<Router basename={process.env.PUBLIC_URL}>{routing}</Router>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
