import './images.css';
import './script.js';
import {Link } from 'react-router-dom'

let images = [
              {
                src: 'https://i.imgur.com/1KKvx9u.jpg'
              },
              {
                src: 'https://i.imgur.com/PPcokDX.jpg'
              },
              {
                src: 'https://i.imgur.com/B6n3JoQ.jpg'
              },
              {
                src: 'https://i.imgur.com/YyFURqP.jpg'
              },
              {
                src: 'https://i.imgur.com/qsgeCtC.jpg'
              },
              {
                src: 'https://i.imgur.com/n0sTCQM.jpg'
              },
              {
                src: 'https://i.imgur.com/tXUqb8A.jpg'
              },
              {
                src: 'https://i.imgur.com/2y13c31.jpg'
              },
              {
                src: 'https://i.imgur.com/lSLL6oA.jpg'
              },
              {
                src: 'https://i.imgur.com/Bai7ngs.jpg'
              },
              {
                src: 'https://i.imgur.com/UA4L95d.jpg'
              },
              {
                src: 'https://i.imgur.com/cygIhn3.jpg'
              },
              {
                src: 'https://i.imgur.com/hbQBcM6.jpg'
              },
              {
                src: 'https://i.imgur.com/nzZwfa8.jpg'
              },
              {
                src: 'https://i.imgur.com/wdmDAck.jpg'
              },
              {
                src: 'https://i.imgur.com/fonho3N.jpg'
              },
]

class Images extends React.Component {
  constructor(props) {
    super(props)


     this.state = {
      images: images

     }
  }



openMenu() {
  let github = document.querySelector('.github_div'),
    linkedin = document.querySelector('.linkedin_div'),
    facebook = document.querySelector('.facebook_div'),
    email = document.querySelector('.email_contact_div'),
    transp_black = document.querySelector('.transp_black'),
    close_contact_menu = document.querySelector('.close_contact_menu'),
    contactSlide = document.getElementById('contact-slide'),
    menu = document.querySelector('.menu'),
    menu_container = document.querySelector('.menu_container');

  // display the menu container (wrapper for menu)

  menu_container.style.display = 'block';
  // display menu and the black transparent background with delay
  setTimeout(function () {
    menu.style.width = '300px';
    transp_black.style.display = 'block';
  }, 500);

  // hide the contact slider if displayed
  setTimeout(function () {
    email.style.marginLeft = '-100%';
  }, 200);
  setTimeout(function () {
    facebook.style.marginLeft = '-100%';
  }, 400);
  setTimeout(function () {
    linkedin.style.marginLeft = '-100%';
  }, 500);
  setTimeout(function () {
    github.style.marginLeft = '-100%';
  }, 800);
  setTimeout(function () {
    close_contact_menu.style.marginLeft = '-15%';

  }, 1000);
  setTimeout(function () {
    contactSlide.style.display = 'none';
  }, 1200);

}

closeMenu() {
  let menu = document.querySelector('.menu'),
    github = document.querySelector('.github_div'),
    transp_black = document.querySelector('.transp_black'),
    menu_container = document.querySelector('.menu_container');

  // hide the menu
  menu.style.width = '0';
  // hide the transparent black background
  transp_black.style.display = 'none';
  // hide the menu container (wrapper for menu) with delay (after hiding the menu);
  setTimeout(function () {
    menu_container.style.display = 'none';
  }, 500);

}
openContact() {
  let github = document.querySelector('.github_div'),
    linkedin = document.querySelector('.linkedin_div'),
    facebook = document.querySelector('.facebook_div'),
    email = document.querySelector('.email_contact_div'),
    transp_black = document.querySelector('.transp_black'),
    close_contact_menu = document.querySelector('.close_contact_menu'),
    contactSlide = document.getElementById('contact-slide');


  this.closeMenu();
  contactSlide.style.display = 'block';

  setTimeout(function () {
    github.style.marginLeft = '0';
    transp_black.style.display = 'block';
  }, 500);
  setTimeout(function () {
    linkedin.style.marginLeft = '0';
  }, 700);
  setTimeout(function () {
    facebook.style.marginLeft = '0';
  }, 900);
  setTimeout(function () {
    email.style.marginLeft = '0';
  }, 1100);
  setTimeout(function () {
    close_contact_menu.style.marginLeft = '98%';
  }, 1400);

}
closeContactMenu() {
  let github = document.querySelector('.github_div'),
    linkedin = document.querySelector('.linkedin_div'),
    facebook = document.querySelector('.facebook_div'),
    email = document.querySelector('.email_contact_div'),
    transp_black = document.querySelector('.transp_black'),
    close_contact_menu = document.querySelector('.close_contact_menu'),
    contactSlide = document.getElementById('contact-slide');


  setTimeout(function () {
    email.style.marginLeft = '-100%';
  }, 400);
  setTimeout(function () {
    facebook.style.marginLeft = '-100%';
  }, 600);
  setTimeout(function () {
    linkedin.style.marginLeft = '-100%';
  }, 800);
  setTimeout(function () {
    github.style.marginLeft = '-100%';
  }, 1000);
  setTimeout(function () {
    close_contact_menu.style.marginLeft = '-15%';
    transp_black.style.display = 'none';
  }, 1300);
  setTimeout(function () {
    contactSlide.style.display = 'none';
  }, 1500);
}


closeTranspBack() {
  this.closeMenu();
  this.closeContactMenu();
}



	render() {

		return (
		<div>
		 <header>
       <nav className='navbar navbar-default col-12' id='navbar'>
          <div className='container-fluid'>
             <div className='navbar-header'>
               <a className='navbar-brand' href={process.env.PUBLIC_URL + '/'}>
               <img src={require('./images/logo.jpg')} alt=''/>
               </a>
              <span>PHOTOGRAPHY</span>
           </div>
          </div>
        </nav>
     </header>

                        {/*--Dark transparent background--*/}

   <div className='transp_black' onClick={() => this.closeTranspBack()}></div>

                    {/* ----------------- MENU -------------- */}

       <div className='row col-12'>
          <div className='wrap_hamb' id='wrap_hamb'>
          <span className='hamb' id='hamb' onClick={() => this.openMenu()}>&#9776;</span>
          </div>
       </div>
       <div className='row col-12'>
         <div className='menu_container'>
           <div className='menu' id='menu'>
              <span className='before_menu_one'></span>
                <span className='close_menu' onClick={() => this.closeMenu()}>&times;</span>
                  <div className='p_menu'>
                    <Link to={process.env.PUBLIC_URL + '/'}>
                     <p className='home_p'>Home</p>
                     </Link>
                       <Link to={process.env.PUBLIC_URL + '/images'}>
                     <p>Gallery</p>
                   </Link>
                  <p id='contact_menu' onClick={() => this.openContact()}>Contact</p>
                </div>
              <span className='before_menu_two'></span>
            </div>
          </div> {/* end of menu container */}
        </div>  {/* end of row for menu */}


     <div className='row' id='contact-slide'>
      <span className='close_contact_menu' onClick={() => this.closeContactMenu()}>&times;</span>
     <div className='col-12'>

     <div className='row contact_row '>
         <div className='contact_div github_div'>
           <a href='https://github.com/SIonut0122'><span className='row github_contact'></span></a>
             <p className='row github_contact_p'>Github</p>
        </div>
     </div>

     <div className='row contact_row'>
         <div className='contact_div linkedin_div'>
          <a href='https://www.linkedin.com/in/stan-ionut-1193a0159/'><span className='row linkedin_contact'></span></a>
            <p className='row'>Linkedin</p>
        </div>
     </div>

     <div className='row contact_row'>
          <div className='contact_div facebook_div'>
            <span className='row facebook_contact'></span>
          <p className='row'>Facebook</p>
        </div>
     </div>

      <div className='row contact_row'>
          <div className='contact_div email_contact_div'>
              <span className='row email_contact'></span>
                <p className='row email_contact_p'>sionut0122@yahoo.com</p>
              </div>
            </div>
         </div>
     </div>

  
  

                {/* --------- IMAGES CONTAINER --------- */}

     <div className='container-fluid'>
          <div className='row'>
              <div className='container_images col-12'>
                    <div className='row justify-content-center align-self-center'>
                        <div className='wrap_images col-12 '>
                            <div className='row justify-content-center align-self-center'>

                  {this.state.images.map((item) =>
                    <div className='img_cont d-inline-block col-xs-12 col-sm-6 col-md-4 col-lg-3 col-xl-3' id='img_cont'>
                        <span className='img_span row'>
                           <a href={item.src} data-lightbox={item.src}><img src={item.src}/></a>
                      </span>
                  </div>
                  )}
               <div className='clear'></div>      
            </div>
          </div>
        </div>
      </div>
    </div>
 </div>




<footer className='container-fluid'>
    <div className='row-fluid col-12'>
        <div className='col-12'>


              <div className='row-fluid'>
                 <div className='col-12 social-footer'>
 
                    <a href='https://github.com/SIonut0122'>
                    <img src={require('./images/github.png')}  
                     alt=''/>
                     </a>

                    <a href='https://www.linkedin.com/in/stan-ionut-1193a0159/'>
                    <img src={require('./images/linkedin.png')}
                    alt=''/>
                    </a>

                    <a href='https://www.facebook.com/StanIonut0122'>
                    <img src={require('./images/facebook.png')} 
                    alt=''/>
                    </a>

                      <p className='allRights col-12'>&#169; Ionut Stan 2019 Design. All rights reserved.</p>
              </div>
           </div>
       </div>
   </div> 
</footer>


		</div>
		)
	}
}

export default Images;